import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { NewBidClientComponent } from './new-bid-client/new-bid-client.component';
import { NewBidComponent } from './new-bid/new-bid.component';

const routes: Routes = [
  {
    path: '',
    component: NewBidComponent,
    children: [
      {
        path: '',
        redirectTo: 'bid_product',
        pathMatch: 'full'
      },
      {
        path: "bid_product",
        component: NewBidClientComponent
      }
    ]
  },
]

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BidModuleRoutingModule { }
